.header {
  height: 60px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__nav {
  opacity: 0;
  transition: transform .3s;
  transform: translateX(-300%);
}

.header__nav.open {
  z-index: 3;
  opacity: 1;
  flex-direction: column;
  display: flex;
  position: absolute;
  top: 180px;
  left: 20px;
  transform: translateX(0);
}

.header__home__link, .header__favorite__link {
  letter-spacing: .02em;
  padding: 0;
  font-family: Poppins;
  font-size: 28px;
  line-height: 1.1;
}

.header__home__link:hover, .header__home__link:focus, .header__favorite__link:hover, .header__favorite__link:focus {
  color: #fd5103;
}

.header__home__link:hover .header__fav-arrow, .header__home__link:focus .header__fav-arrow, .header__favorite__link:hover .header__fav-arrow, .header__favorite__link:focus .header__fav-arrow {
  fill: #fd5103;
}

.header__home__link {
  margin-bottom: 32px;
}

.js-menu {
  margin-top: 0;
}

.header__cocktails__item {
  letter-spacing: .02em;
  background-color: #fff;
  border: 1px solid #fd5103;
