  color: #fff;
}

.input {
  pointer-events: none;
  opacity: 0;
  width: 100%;
  transition: all .3s;
  position: absolute;
  top: 90px;
  left: -12px;
  transform: translateX(-400%);
}

.input.open {
  pointer-events: all;
  z-index: 3;
  opacity: 1;
  display: flex;
  position: absolute;
  transform: translateX(0);
}

.input__btn {
  width: 40px;
  height: 40px;
  z-index: 2;
  cursor: pointer;
  background-color: #0000;
  border: 0;
  border-right: 1px solid #fd5103;
  position: relative;
  left: 30px;
}

.input__icon {
  z-index: 1;
  margin-right: -30px;
  position: relative;
  top: 9px;
}

.input__search {
  width: 94%;
  height: 40px;
  caret-color: #5f6775;
  border: 1px solid #fd5103;
  border-radius: 4px;
  padding-left: 44px;
}

.input__search::placeholder {
