}

.input__search:focus {
  outline: 1px solid #fe7031;
}

.menu__icon {
  cursor: pointer;
  margin-top: 12px;
}

.menu__mobile {
  visibility: hidden;
  opacity: 0;
  height: 100vh;
  width: 100%;
  z-index: 2;
  background-color: #fff;
  transition: all .3s;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.menu__mobile.open {
  visibility: visible;
  opacity: 1;
  flex-direction: column;
  display: flex;
  transform: translateX(0);
}

.menu__box {
  height: 60px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
}

.menu__logo {
  display: none;
}

.menu__close {
  cursor: pointer;
  background-color: #0000;
  border: 0;
  margin-top: 12px;
  margin-left: auto;
  left: 90%;
}

.overflow {
  overflow: hidden;
}

.header__favorite__bar {
  visibility: hidden;
  opacity: 0;
  z-index: 2;
  background-color: #fff;
  border-radius: 5px;
  transition: all .3s;
  position: relative;
  top: 10px;
  left: 30px;
  overflow: hidden;
  transform: scaleX(0);
}

.header__favorite__bar.active {
  visibility: visible;
  opacity: 1;
  transform: scaleX(1);
}

.header__fav-arrow {
  color: #000;
  fill: currentColor;
  margin-left: 10px;
}

.header__fav-arrow:hover, .header__fav-arrow:focus {
  color: #5f6775;
}

@media screen and (min-width: 767.98px) {
  .header__nav.open {
    left: 45%;
  }

  .input {
    opacity: 1;
    width: 288px;
    pointer-events: all;
    align-items: center;
    margin-top: 10px;
    margin-left: auto;
    margin-right: 40px;
    transition: none;
    display: flex;
    position: relative;
    top: 0;
    left: 0;
    transform: none;
  }

  .input.open {
    display: none;
  }

  .input__icon {
    top: 0;
  }

  .menu__box {
    width: 768px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 32px;
    padding-right: 32px;
  }

  .menu__logo {
    margin-right: 60px;
    display: block;
  }

  .nav__mobile {
