    transform: translate(-50%, -50%)scaleX(0);
  }

  .header__favorite__bar.active {
    opacity: 1;
    transform: translate(0)scaleX(1);
  }

  .header__fav-arrow, .menu {
    display: none;
  }

  .input {
    pointer-events: all;
    width: 288px;
    opacity: 1;
    margin-right: 170px;
    position: relative;
    top: 0;
    left: 0;
    transform: translateX(0);
  }

  .header__nav {
    opacity: 1;
    justify-content: center;
    align-items: center;
    gap: 60px;
    margin-top: 15px;
    margin-left: 43px;
    font-size: 14px;
    line-height: 1.3;
    transition: none;
    display: flex;
    position: relative;
    transform: none;
  }

  .header__home__link, .header__favorite__link {
