.switch {
  width: 40px;
  height: 32px;
  border-radius: 50em;
  flex-shrink: 0;
  align-items: center;
  padding: 3px 0;
  display: flex;
  position: relative;
}

.switch__input, .switch__label {
  position: absolute;
  top: 0;
  left: 0;
}

.switch__input {
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
  margin: 0;
  padding: 0;
}

.switch__input:checked + .switch__label {
  background-color: #fd5103;
}

.switch__input:checked + .switch__label + .switch__marker {
  background-color: #fff;
  left: 20px;
}

.switch__label {
  width: 40px;
  height: 20px;
  color: #0000;
  -webkit-user-select: none;
  user-select: none;
  border-radius: inherit;
  z-index: 1;
  background-color: #0000;
  border: 1px solid #fd5103;
  transition: background 1s;
}

.switch__label:hover, .switch__label:focus {
  cursor: pointer;
}

.switch__marker {
  width: 16px;
  height: 16px;
  z-index: 2;
  pointer-events: none;
  will-change: left;
  background-color: #fd5103;
  border-radius: 50%;
  transition: left 1s;
  position: relative;
  bottom: 6px;
  left: 3px;
  box-shadow: 0 1px 1px #00000040;
}

